
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data(){
		return {
			userInfo:{}
		}
	},
	methods: {
		...mapMutations(['login','setToken'])
	},
	computed:{
		 ...mapState(['token']),
		
	},
	onLaunch: function() {





		if(this.token){
			//更新登陆状态
			uni.getStorage({
				key: 'userInfo',
				success: (res) => {
					this.login(res.data);
				}
			});
			uni.getStorage({
				key: 'token',
				success: (res) => {
					this.setToken(res.data);
				}
			});
		}else{
			uni.reLaunch({
				url: '/pages/login/login'
			});
		}

		
	},
	onShow: function() {
		// console.log('App Show')
	},
	onHide: function() {
		// console.log('App Hide')
	},
}
